<template>
  <div ref="demo"></div>
</template>

<script>
  import * as THREE from 'three'
  export default {
    data: () => ({
      controls: {
        scene: null,
        camera: null,
        renderer: null,
        geometry:null,
        material:null,
        mesh:null,
        rotationSpeed: 0.02
      }
    }),
    created () {
      this.$nextTick(() => {
        this.init()
      })
    },
    methods: {
      init () {
        let {initMesh, controls} = this
        initMesh()
      },
      initMesh () {
        this.scene = new THREE.Scene() // 场景
        this.camera  = new THREE.PerspectiveCamera( 70, window.innerWidth / window.innerHeight, 0.01, 10 );
        this.camera.position.z = 5;

        this.geometry = new THREE.BoxGeometry( 2, 1, 1 );
        this.material = new THREE.MeshNormalMaterial();

        this.mesh = new THREE.Mesh( this.geometry, this.material );
        this.scene.add( this.mesh );

        this.renderer = new THREE.WebGLRenderer( { antialias: true } );
        this.renderer.setClearColor(0xffffff);
        this.renderer.setSize( window.innerWidth-100, window.innerHeight-120);

        this.$refs.demo.append(this.renderer.domElement)

        this.renderScene()
      },
      renderScene () {
        let {controls, mesh, scene, camera, renderer} = this
        //mesh.rotation.x += controls.rotationSpeed
        //mesh.rotation.y += controls.rotationSpeed
        //mesh.rotation.z += controls.rotationSpeed
        mesh.rotation.x += 0.01;
        mesh.rotation.y += 0.02;
        requestAnimationFrame(this.renderScene)
        renderer.render(scene, camera)
      }
    }
  }
